        },

        //ID
        GetDeceteInfoById(id){
            let that = this;
            that.axios.get('/api/NHL/GetDeceteInfoById',{
            params: {
                id:id,
            }
            }).then(function (res) {
                console.log(res.data);
                if(res.data.s){
                    console.log(111)
                    if(res.data.data && res.data.data !=''){
                        console.log(res.data.data)
                        if(!res.data.data.detectcode || res.data.data.detectcode == 'null'){
                            res.data.data.detectcode = '--'
                        };
                        if(res.data.data.sex  == ""){
                          res.data.data.sex = '1'
                        }else{
                          res.data.data.sex = '2'
                        }
                        if(res.data.data.write){
                          res.data.data.write = that.timeStrChange(res.data.data.write*1); 
                        }else{
                          res.data.data.write = that.timeStrChange(res.data.data.u_time); 
                        }
                        switch (res.data.data.diseaseSubtype) {//
                            case 'H1L01':
                                    res.data.data.diseaseSubtype = 'B-ALL(B)'
                                break;
                            case 'H1L02':
                                    res.data.data.diseaseSubtype = 'T-ALL(T)'
                                break;
                            case 'H1L03':
                                    res.data.data.diseaseSubtype = 'AML()'
                                break;
                            case 'H1L04':
                                    res.data.data.diseaseSubtype = 'APL()'
                                break;
                            case 'H1L05':
                                    res.data.data.diseaseSubtype = 'MDS()'
                                break;
                            case 'H1L06':
                                    res.data.data.diseaseSubtype = 'MPN()'
                                break;
                            case 'H1L07':
                                    res.data.data.diseaseSubtype = 'CML()'
                                break;
                            case 'H1L08':
                                    res.data.data.diseaseSubtype = 'MDS/MPN(MDSMPN)'
                                break;
                            case 'H1L09':
                                    res.data.data.diseaseSubtype = 'AML(tAML)'
                                break;
                            case 'H1L10':
                                    res.data.data.diseaseSubtype = ''
                                break;
                            case 'H1L11':
                                    res.data.data.diseaseSubtype = 'AA()'
                                break;
                            case 'H1L12':
                                    res.data.data.diseaseSubtype = ' '
                                break;
                            case 'H1L13':
                                    res.data.data.diseaseSubtype = ' '
                                break;
                            case 'H1S01':
                                    res.data.data.diseaseSubtype = ''
                                break;
                            case 'H1B01':
                                    res.data.data.diseaseSubtype = 'DLBCL(B)'
                                break;
                            case 'H1B02':
                                    res.data.data.diseaseSubtype = 'FL()'
                                break;
                            case 'H1B03':
                                    res.data.data.diseaseSubtype = 'MCL()'
                                break;
                            case 'H1B04':
                                    res.data.data.diseaseSubtype = 'CLL/SLL(/)'
                                break;
                            case 'H1B05':
                                    res.data.data.diseaseSubtype = 'LPL/WM(/)'
                                break;
                            case 'H1B06':
                                    res.data.data.diseaseSubtype = 'B '
                                break;
                            case 'H1B07':
                                    res.data.data.diseaseSubtype = 'B '
                                break;
                            case 'H1T01':
                                    res.data.data.diseaseSubtype = 'AITL(T)'
                                break;
                            case 'H1T02':
                                    res.data.data.diseaseSubtype = 'ALCL()'
                                break;
                            case 'H1T03':
                                    res.data.data.diseaseSubtype = 'PTCL-NOS(T)'
                                break;
                            case 'H1T04':
                                    res.data.data.diseaseSubtype = 'NK/T(NK/T-CL)'
                                break;
                            case 'H1T05':
                                    res.data.data.diseaseSubtype = 'T '
                                break;
                            case 'H1T06':
                                    res.data.data.diseaseSubtype = 'T '
                                break;
                            case 'H1G01':
                                    res.data.data.diseaseSubtype = '(MM)'
                                break;
                            case 'H2H01':
                                    res.data.data.diseaseSubtype = ''
                                break;
                            default:
                                break;
                        };
                        if(res.data.data.ldh == ''){//LDH()
                            res.data.data.ldh='+';
                        }else if(res.data.data.ldh == ''){
                            res.data.data.ldh='-';
                        };
                        if(res.data.data.myc == ''){//MYC
                            res.data.data.myc='+';
                        }else if(res.data.data.myc == ''){
                            res.data.data.myc='-';
                        };
                        if(res.data.data.bcl2 == ''){//BCL2
                            res.data.data.bcl2='+';
                        }else if(res.data.data.bcl2 == ''){
                            res.data.data.bcl2='-';
                        };
                        that.recordList =  res.data.data;
                        if(res.data.data.formid && res.data.data.formid !=''){
                          that.followList.formid = res.data.data.formid;
                          that.getDdetectByFromid(res.data.data.formid);
                          that.getFollowByFromid(res.data.data.formid);
                        }
                    }
                }
            })
        },

        //formid
        getDdetectByFromid(formid){
           let that = this;
            that.axios.get('/api/NHL/GetDetectByFromId',{
