import { Switch } from 'vant';
import wx from "weixin-js-sdk";
import { Swipe, SwipeItem } from 'vant';

Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(Switch);
Vue.use(Dialog);

export default {
    name:'',
    data(){
        return{
            userInfoList:{
