            },
            logisticsCompanySelect:['',''],
            hospitalSelect:[],
            slice_countSelect:['10','11','12','13','14','15'],
            quyangjieduanSelect:['','','','','_'],
            datePickerIndex:null,
            adduser:{
                detectcode:'',//
                username:'',//
                sex:'1',//
                age:'',//
                zhenduan:'DLBCL',//
                unit:'',//
                submissiondate:'',//
                slice_parts:'',//
                slice_count:'',//
                quyangjieduan:'',
                quyangjieduanqita:'',
                mianyizuhuajiegou:'',//
                songjianqianzhiliaofangan:'',//
                receiveemail:'',//
                ems:'',//
                ems_code:'',//
                ems_date:'',//
                agree_img:'',//
                status:'',// 0 1
                sample_need_return:true,//
                sample_return_name:'',//
                sample_return_addressee:'',//
                sample_return_phone:''//
            },
            id:null,
            organization:null,
            checkDetectcode:true,
            yearDate:null,
            openid:'',
            fileurlA:'',
            client:null,
            isDisabled:false,
            isUpdata:false,
            loadingShow:false,
            showImgList:[],
            ruiangClick:false,
            selectIndex:'1',
            month:'',
           
        }
    },
    components:{
      'vanPickerSelect':vanPickerSelect,
      'vanDatePickerSelect':vanDatePickerSelect,
      'loading':loading
    },
    mounted(){
        var query=this.$route.query;
        if(query.id){
            this.id = query.id;
            this.organization = query.organization;
            this.GetDeceteInfoById(query.id);
        }else{
            this.id = null;
            this.organization = null;
            this.getjinxiangCode();
        };
        if(query.openid){
            this.openid = query.openid;
            this.getuserInfoChange();
        };
        
        const date=new Date;
        const month=date.getMonth()+1;
        this.month =(month<10 ? "0"+month:month); 
        this.yearDate=this.timeStrChange(new Date().getTime());
    },
    methods:{
        //
        getjinxiangCode(){
            let that = this;
            that.axios.get('/api/NHL/GetJinXiangDetectCode').then(function (res){
                console.log(res.data);
                if(res.data.data.code && res.data.data.code !=''){
                    //that.adduser.detectcode = res.data.data.code;
                }
            })
        },
        //
        getuserInfoChange(){
            let that = this;
            that.axios.get('/api/NHL/GetUserInfo',{
            params: {
                openid:that.openid,
            }
            }).then(function (res) {
                console.log(res.data);
                if(res.data.s){
                    if(res.data.data && res.data.data !=''){
                        if(that.isUpdata){
                            return
                        }else{
                            that.userInfoList = res.data.data;
                            if(res.data.data.email){
                                that.adduser.receiveemail=res.data.data.email; //  
                            }
                        }
                        console.log(that.userInfoList)
                    }
                }
            })
        },
        
        //ID
        GetDeceteInfoById(id){
            let that = this;
            //TODO
            that.axios.get('/api/NHL/GetInfoDetailsByIdV3',{
            params: {
                id:id,
                organization:'1'
            }
            }).then(function (res) {
                console.log(res.data);
                if(res.data.s){
                    console.log(111)
                    if(res.data.data && res.data.data !=''){
                        console.log(res.data.data)
                        if(res.data.data.username.sex == ""){
                            that.adduser.sex = '1'
                        }else if(res.data.data.username.sex == ""){
                            that.adduser.sex = '2'
                        }
                        that.isUpdata = true;
                        that.adduser.detectcode=res.data.data.detectcode;
                        that.adduser.username=res.data.data.patient;
                        that.userInfoList.nickname = res.data.data.username;
                        that.adduser.age=res.data.data.age;
                        that.adduser.zhenduan=res.data.data.zhenduan;
                        that.adduser.unit=res.data.data.unit;
                        that.adduser.submissiondate = res.data.data.submissiondate.replace("00:00:00","");//
                        that.adduser.slice_parts = res.data.data.slice_parts;//
                        that.adduser.slice_count = res.data.data.slice_count;//
                        that.showImgList = JSON.parse(res.data.data.mianyizuhuajiegou);//
                        that.adduser.songjianqianzhiliaofangan = res.data.data.songjianqianzhiliaofangan;//
                        if(res.data.data.receiveemail){
                            that.adduser.receiveemail=res.data.data.receiveemail; //  
                        }
                        that.adduser.agree_img = res.data.data.agree_img;//
                        that.adduser.ems_date=res.data.data.ems_date.replace("00:00:00","");//
                        that.adduser.ems_code=res.data.data.ems_code;//
                        that.adduser.ems = res.data.data.ems//
                        that.adduser.sample_need_return = res.data.data.sample_need_return;//
                        that.adduser.sample_return_name= res.data.data.sample_return_name;//
                        that.adduser.sample_return_addressee=res.data.data.sample_return_addressee;//
                        that.adduser.sample_return_phone=res.data.data.sample_return_phone;//
                        console.log( that.adduser);
                    }else{
                        that.isUpdata =false;
                    }
                }
            })
        },

        //
        checkCodeChange(){
            let that = this;
            that.axios.get('/api/NHL/CheckCode',{
            params: {
                code:'JX0'+that.month+that.adduser.detectcode,
                type:'1'//0G-02,B+1:B+2:
            }
            }).then(function (res) {
                console.log(res.data);
                if(res.data.s){
                   that.checkDetectcode = true;
                }else{
                    that.checkDetectcode = false;
                    Dialog.alert({
                        title: '',
                            message: res.data.msg,
                        }).then(() => {
                        // on close
                    });
                }
            })
        },

        //
        searchCompany(val){
            console.log(val)
            this.initSearch(val);
        },

        //
        initSearch(hospitalVal){
            let that = this;
            that.axios.get('/api/NHL/GetUnit',{
            params: {
                hospital:hospitalVal,
            }
            }).then(function (res) {
                console.log(res.data);
                if(res.data.s){
                    that.hospitalSelect = [];
                    if(res.data.data && res.data.data !=''){
                        res.data.data.forEach(element => {
                            that.hospitalSelect.push(element.hospital);
                        });
                        that.$refs.PickerSelect.onPickerConfirm(that.hospitalSelect);
                        that.selectIndex = '9'
                    }else{
                        that.hospitalSelect = [];
                    }
                }
            })
        },

        //
        slice_countChange(){
           this.$refs.PickerSelect.onPickerConfirm(this.slice_countSelect);
           this.selectIndex = '3' 
        },

        //
        quyangjieduanChange(){
            this.$refs.PickerSelect.onPickerConfirm(this.quyangjieduanSelect);
            this.selectIndex = '4' 
        },

        //
        closeImgChange(tem){
            console.log(tem)
            this.showImgList.splice(this.showImgList.findIndex(item => item.imgSrc === tem.imgSrc), 1)
        },

        //
        emsChange(){
            this.$refs.PickerSelect.onPickerConfirm(this.logisticsCompanySelect);
            this.selectIndex = '2'
        },

        //
        onPickerConfirm(res){
            tihs.$emit(res);
        },

        //
        getMsgFormSon(data){
            console.log(data);
            let that  = this;
            console.log(that.selectIndex);
            switch (that.selectIndex) {
                case '2':
                    that.adduser.ems = data;
                    break;
                case '3':
                    that.adduser.slice_count = data;
                    break;
                case '4':
                    that.adduser.quyangjieduan = data;
                    break;
                case '9':
                    that.adduser.unit = data;
                    break;      
                default:
                    break;
            }

        },
        
        //
        SubmissionDateChange(){
            this.$refs.datePickerSelect.onDatePickerConfirm();
            this.datePickerIndex = '1';
        },

        //
        MailChange(){
            this.$refs.datePickerSelect.onDatePickerConfirm();
            this.datePickerIndex = '2';
        },

        //
        onDatePickerConfirm(){
          tihs.$emit();
        },

        //
        getDateMsgFormSon(data){
          console.log(data);
          switch (this.datePickerIndex) {
            case '1':
                  this.adduser.submissiondate  = data;   
                break;
            case '2':
                  this.adduser.ems_date  = data;   
                break;
            default:
                break;
          }
         
        },
        
        //
        imgUploadChange(type){
            console.log(type)
            if(type == 'A'){
                this.$refs.inputA.dispatchEvent(new MouseEvent('click'));
            }else if(type == 'B'){
                this.$refs.inputB.dispatchEvent(new MouseEvent('click'));
            }
        },

        //
        changeImg (type) {
            if(type =='A'){
                var files = this.$refs.inputA.files;
                this.Upload(files[0],type);
            }else{
                var files = this.$refs.inputB.files;
                this.Upload(files[0],type);
            }
           
        },

        //..
        Upload(file,type) {
            this.loadingShow = true;
            var name = 'linbaliu'
            let fileName = name+'/'+file.lastModified + file.name;
            const _this = this;
            _this.axios.get('https://commapi.forhoo.cn/forhoo/GetOSSSTS?dir=linbaliu').then((result) => {
                _this.client =  new OSS({
                    region: result.data.region,
                    accessKeyId: result.data.data.accessKeyId,
                    accessKeySecret: result.data.data.accessKeySecret,
                    stsToken: result.data.data.securityToken,
                    bucket: result.data.bucketName
                });
                this.getBase64(file).then(resBase64 => {
                    const base64 = resBase64.split(",").pop();
                    const fileType = resBase64.split(";").shift().split(":").pop();
                    // base64blob
                    const blob = _this.dataURLtoBlob(base64, fileType);
                    // blobarrayBuffer
                    const reader = new FileReader();
                    reader.readAsArrayBuffer(blob);
                    reader.onload = event => {
                        //arrayBufferBuffer
                        const buffer = OSS.Buffer(event.target.result);
                        // 
                        _this.client.put(fileName, buffer)
                        .then( result=> {
                            console.log(result);
                            let httpUrl = 'https://merckoss.wfbweb.com/';
                            if(type == 'A'){
                              const obj = {
                                imgSrc:httpUrl+fileName
                              }
                              _this.showImgList.push(obj);
                              console.log(_this.showImgList)
                            }else{
                                _this.adduser.agree_img =  httpUrl+fileName;
                            }
                            _this.loadingShow = false;
                        })
                        .catch( err=> {
                            console.log("", err);
                        });
                    };
                })
            })
        },

        //filebase64
        getBase64(file) {
            return new Promise((resolve, reject) => {
                let reader = new FileReader();
                let fileResult = "";
                reader.readAsDataURL(file);
        //
                reader.onload = function() {
                    fileResult = reader.result;
                };
        // 
                reader.onerror = function(error) {
                reject(error);
                };
        //  
                reader.onloadend = function() {
                resolve(fileResult);
                };
            });
        },
        
        //base64blob
        dataURLtoBlob(urlData, fileType) {
            let bytes = window.atob(urlData);
            let n = bytes.length;
            let u8arr = new Uint8Array(n);
            while (n--) {
                u8arr[n] = bytes.charCodeAt(n);
            }
            return new Blob([u8arr], {
                type: fileType
            });
        },

        //
        addSubmit(){
            console.log(111)
            if(!this.checkDetectcode){
                Dialog.alert({
                    title: '',
                    message: '',
                    }).then(() => {
                    // on close

                });
            }else if(this.adduser.detectcode ==''){
                Dialog.alert({
                    title: '',
                    message: '',
                    }).then(() => {
                    // on close

                });
            }else if(this.adduser.username ==''){
                Dialog.alert({
                    title: '',
                    message: '',
                    }).then(() => {
                    // on close

                });
            }else if(this.adduser.age ==''){
                Dialog.alert({
                    title: '',
                    message: '',
                    }).then(() => {
                    // on close

                });
            }else if(this.adduser.zhenduan ==''){
                Dialog.alert({
                    title: '',
                    message: '',
                    }).then(() => {
                    // on close

                });
            }else if(this.adduser.unit ==''){
                Dialog.alert({
