import loading from '@/components/loading'
import OSS from 'ali-oss';
import Vue from 'vue'
import { Dialog } from 'vant';
import { Switch } from 'vant';
import wx from "weixin-js-sdk";
Vue.use(Switch);
Vue.use(Dialog);
export default {
    name:'',
    data(){
        return{
            DepartmentSelect:['','',''],
