            // DiseaseSubtypeSelect:['B-ALL(B)','T-ALL(T)','AML()','APL()','MDS()','MPN()','CML()','MDS/MPN(MDSMPN)','AML(tAML)','','AA()',' ',' ','','DLBCL(B)','FL()','MCL()','CLL/SLL(/)','LPL/WM(/)','B ','B ','AITL(T)','ALCL()','PTCL-NOS(T)','NK/T(NK/T-CL)','T ','T ','(MM)',''],
            DiseaseSubtypeSelect:['DLBCL(B)'],
            ecogSelect:['0','1','2','3','4'],
            userInfoList:{
                nickname:''
            },
            adduser:{
                detectcode:'',
                username:'',
                sex:'1',
                age:'',
                sendingDoctor:'',//
                sendingCompany:'',//
                department:'',//
                clinical:'/DLBCL',///
                primarySite:'',//
                courseDisease:'',//
                AnnArbor:'I',//AnnArbor()
                diseaseStage:'I',//
                extranodalInfiltration:'',//
                ECOG:'',//ECOG()
                LDH:'+',//LDH()
                ldh_exp:'',//LDH()
                MYC:'+',//MYC
                MYCCascade:'',//MYC()
                BCL2:'+',//BCL2
                BCL2Cascade:'',//BCL2()
                sampleType:'',//
                agree_img:'',
                ems:'',//
                ems_code:'',//
                ems_date:'',//
                patientphone:'',// 
                diseaseSubtype:'DLBCL(B)',// 
                sample_need_return:true,//
                sample_return_name:'',//
                sample_return_addressee:'',//
                sample_return_phone:''//
            },
            id:null,
            organization:null,
            checkDetectcode:true,
            yearDate:null,
            selectIndex:'1',
            openid:'',
            fileurlA:'',
            client:null,
            isDisabled:false,
            isUpdata:false,
            loadingShow:false,
            ruiangClick:true,
            joinType:''
        }
    },
    components:{
      'vanPickerSelect':vanPickerSelect,
      'vanDatePickerSelect':vanDatePickerSelect,
      'loading':loading
    },
    mounted(){
        var query=this.$route.query;
        if(query.id){
            this.id = query.id;
            this.organization = query.organization;
            this.GetDeceteInfoById(query.id);
        }else{
            this.id = null;
            
        };
        if(query.openid){
            this.openid = query.openid;
            this.getuserInfoChange();
        };
        if(query.joinType){
            console.log(query.joinType)
            this.joinType = query.joinType;
        }
        this.yearDate=this.timeStrChange(new Date().getTime());
    },
    methods:{
        jingxiangChange(){
            if(this.ruiangClick){
                if(this.openid && this.id){
                    Dialog.alert({
                        title: '',
                        message: '',
                        }).then(() => {
                        // on close

                    });
                }else if(this.openid){
                    this.$router.replace({ path: "/recordList",query: {openid:this.openid}});
                }
            }else{
                Dialog.alert({
                    title: '',
                    message: '',
                    }).then(() => {
                    // on close

                });
            }
        },

        //
        getuserInfoChange(){
            let that = this;
            that.axios.get('/api/NHL/GetUserInfo',{
            params: {
                openid:that.openid,
            }
            }).then(function (res) {
                console.log(res.data);
                if(res.data.s){
                    console.log(111)
                    if(res.data.data && res.data.data !=''){
                        that.organization = res.data.data.organization;
                        if(that.isUpdata){
                            return
                        }else{
                            that.userInfoList = res.data.data;
                        }
                        that.organization  = es.data.data.organization;
                        if(res.data.data.organization == '0'){
                            that.ruiangClick = false;
                            that.organization = '0';
                        }else if(res.data.data.organization == '0,2'){
                            that.ruiangClick = false;
                            that.organization = '0,2';
                        }else{
                            if(res.data.data.organization == '1' || res.data.data.organization == '0,1'){
                                that.organization = '1';
                                that.ruiangClick = true;
                            }else if(res.data.data.organization == '2' ){
                                that.organization = '2';
                            }else{
                                that.organization = '3';
                            }
                            that.ruiangClick = true;
                        }
                        console.log(that.userInfoList)
                    }
                }
            })
        },
        
        //ID
        GetDeceteInfoById(id){
            let that = this;  
            that.axios.get('/api/NHL/GetInfoDetailsByIdV3',{
            params: {
                id:id,
                organization:'0'
            }
            }).then(function (res) {
                console.log(res.data);
                if(res.data.s){
                    console.log(111)
                    if(res.data.data && res.data.data !=''){
                        console.log(res.data.data)
                        if(res.data.data.username.sex == ""){
                            that.adduser.sex = '1'
                        }else if(res.data.data.username.sex == ""){
                            that.adduser.sex = '2'
                        }
                        that.isUpdata = true;
                        that.adduser.detectcode=res.data.data.detectcode;
                        that.adduser.username=res.data.data.patient;
                        that.userInfoList.nickname = res.data.data.username;
                        that.adduser.age=res.data.data.age;
                        that.adduser.sendingDoctor=res.data.data.doctor;//
                        that.adduser.sendingCompany=res.data.data.unit;//
                        that.adduser.department=res.data.data.keshi;//
                        that.adduser.clinical=res.data.data.zhenduan;///
                        that.adduser.primarySite=res.data.data.part;//
                        that.adduser.courseDisease=res.data.data.sick;//
                        that.adduser.AnnArbor=res.data.data.ann;//AnnArbor()
                        that.adduser.diseaseStage=res.data.data.disease;//
                        that.adduser.extranodalInfiltration=res.data.data.qinrun;//
                        that.adduser.ECOG=res.data.data.ecog;//ECOG()
                        
                        if(res.data.data.ldh == ''){//LDH()
                            that.adduser.LDH='+';
                        }else if(res.data.data.ldh == ''){
                            that.adduser.LDH='-';
                        }else{
                            that.adduser.LDH=res.data.data.ldh;
                        };
                        that.adduser.ldh_exp = res.data.data.ldh_exp;//LDH()

