                        that.adduser.agree_img=res.data.data.agree_img;
                        that.adduser.ems=res.data.data.ems;//
                        that.adduser.ems_code=res.data.data.ems_code;//
                        if(res.data.data.ems_date){
                            that.adduser.ems_date=res.data.data.ems_date.replace("00:00:00","");//
                        }
                        that.adduser.sample_need_return = res.data.data.sample_need_return;//
                        that.adduser.sample_return_name= res.data.data.sample_return_name;//
                        that.adduser.sample_return_addressee=res.data.data.sample_return_addressee;//
                        that.adduser.sample_return_phone=res.data.data.sample_return_phone;//
                        that.adduser.patientphone = res.data.data.patientphone//
                        switch (res.data.data.diseaseSubtype) {//
                            case 'H1L01':
                                    that.adduser.diseaseSubtype = 'B-ALL(B)'
                                break;
                            case 'H1L02':
                                    that.adduser.diseaseSubtype = 'T-ALL(T)'
                                break;
                            case 'H1L03':
                                    that.adduser.diseaseSubtype = 'AML()'
                                break;
                            case 'H1L04':
                                    that.adduser.diseaseSubtype = 'APL()'
                                break;
                            case 'H1L05':
                                    that.adduser.diseaseSubtype = 'MDS()'
                                break;
                            case 'H1L06':
                                    that.adduser.diseaseSubtype = 'MPN()'
                                break;
                            case 'H1L07':
